.login-box {
  position: relative;
  /* top: 50%; */
  /* left: 50%; */
  width: 40vh;
  min-width: 380px;
  padding: 40px;
  /* transform: translate(-50%, -50%); */
  /* background: rgba(0, 0, 0, 0.5); */
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  /* margin-left: 30%; */
}

.login-box h2 {
  margin: 0 0 30px;
  padding: 0;
  color: #ff4c29;
  text-align: center;
  font-family: "Ubuntu Mono", monospace;
}

.login-box h3 {
  margin: 0 0 30px;
  padding: 0;
  color: #fff;
  text-align: center;
}

.login-box .user-box {
  position: relative;
}

.login-box .user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
  font-family: "Kanit", sans-serif;
}

.login-box .user-box textarea {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
  font-family: "Kanit", sans-serif;
  resize: none;
}

.login-box .user-box label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  pointer-events: none;
  transition: 0.5s;
  font-family: "Glegoo", serif;
}

.login-box .user-box input:focus + label,
.login-box .user-box input:not(:placeholder-shown) + label {
  /* top: -20px; */
  left: 0;
  color: #ff4c29;
  font-size: 12px;
  transform: translate(0, -1.5rem);
}

.login-box .user-box textarea:focus + label,
.login-box .user-box textarea:not(:placeholder-shown) + label {
  /* top: -20px; */
  left: 0;
  color: #ff4c29;
  font-size: 12px;
  transform: translate(0, -1.5rem);
}

.login-box form button {
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  color: #ff4c29;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 40px;
  letter-spacing: 4px;
  font-family: "Glegoo", serif;
}

.login-box button:hover {
  background: #ff4c29;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #ff4c29, 0 0 25px #ff4c29, 0 0 50px #ff4c29,
    0 0 100px #ff4c29;
}

.login-box button span {
  position: absolute;
  display: block;
}

.login-box button span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #ff4c29);
  animation: btn-anim1 1s linear infinite;
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.login-box button span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #ff4c29);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.login-box button span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #ff4c29);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.login-box button span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #ff4c29);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
